.header-container {
  display: flex;
  flex-direction: column;
  height: 80px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
  box-sizing: border-box;
  top: 0;
}

.header-title {
  font-size: 1.75em;
  font-weight: bold;
  margin: 0 10px 0 10px;
  text-align: center;
  text-shadow: 0 1px 1  rgb(11, 41, 46);
}
.header-links {
  display: flex;
  flex-direction: row;
  align-items: space-between;
  justify-content: center;
  height: 100%;
  width: 100%;
  margin-top: 7px;
  cursor: pointer;
}
.header-link {
  font-size: 1em;
  margin: 0 auto;
  text-decoration: none;
}
