.options {
  font-size: 1.25em;
  height: 100%;
  width: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.content {
  margin: 20px auto;
  /*display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;*/
}
.email a {
  text-decoration: none;
}
.email a:hover {
  text-decoration: none;
  color: rgb(62, 22, 22);
}
.email a:visited {
  text-decoration: none;
}
.option {
  margin: 10px 0;
}
.options-title {
  text-align: center;
  font-size: 1.5em;
}
.option-label {
  font-size: 1.2em;
}
.option-checkbox {
  font-size: 1.25em;
  cursor: pointer;
  margin: 5px 8px;
  display: inline-block;
}
#status {
  font-size: .85em;
  margin: auto;
  text-align: center;
  opacity: .6;
}
#status2 {
  font-size: 1.05em;
  margin: auto;
  text-align: center;
  opacity: .6;
}
.save-btn {
  background: #eef3f0;
  color: #000000;
  border: 0;
  border-radius: 5px;
  padding: 8px 30px;
  font-size: 1.3em;
  margin: 25px auto 0 auto; 
  display: block;
  cursor: pointer;
}